.filter-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: row-reverse;
  background-color: $gray-ultra-lightest;

  &__inner {
    flex-basis: 100%;
    max-width: 1060px;
  }

  &--bg-white {
    background-color: #fff;
  }
}
