//         - embed-responsive,  ,
// , , , @media-...
//     .

.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;

  &::before {
    content: "";
    display: block;
    padding-top: percentage(3 / 4);
  }

  iframe,
  embed,
  object,
  video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }





  &--16-9 {
    &::before {
      padding-top: percentage(9 / 16);
    }
  }

  &--1-1 {
    &::before {
      padding-top: percentage(1 / 1);
    }
  }
}
