//         - card-map-wrap,  ,
// , , , @media-...
//     .

.card-map-wrap {
  position: absolute;
  transform: translateX(-50%);

  &__item {

    @media (min-width: $screen-lg) {
      display: none;
    }

    &--hide-mobile {

      @media (max-width: ($screen-lg - 1)) {
        display: none !important;
      }
    }

    &--shown {

      @media (min-width: $screen-lg) {
        display: block;
      }
    }
  }
}
