$line-height:                 1.375em !default;

$text-color:                  hsl(0, 0%, 10%) !default;

.breadcrumbs {

  &__item {

    &:not(:last-child) {

      &:after {
        content: '>';
        display: inline-block;
        margin: 0 0.3em 0 0.6em;
        color: $gray;
      }
    }
  }

